import { Link } from 'react-router-dom';
import notFound from '../../assets/404.png';

function NotFound() {
  return(
    <div className="not-found">
      <img src={notFound} alt="not-found-404" />

      <h1>
        ,     
      </h1>

      <span>    </span>

      <h3><Link to="/"> </Link></h3>

    </div>
  )
}

export default NotFound;
