import { useForm } from "react-hook-form";

const FillUserData = ({onSubmit}) => {
  const { register, handleSubmit } = useForm();

  const genders = [
    {
      value: "",
      label:""
    },
    {
      value: "",
      label:""
    }
  ];

  return (
    <div className="empty-data">
        <h2> </h2>

        <form onSubmit={handleSubmit(onSubmit)} >
          <label></label>
          <input {...register("", { required: true })} />

          <label></label>
          <select {...register("")}>
            {genders.map((gender) => (
              <option 
                key={gender.value}
                value={gender.value}>
                  {gender.label}
              </option>
            ))}
          </select>

          <label> </label>
          <input {...register("", { required: true })} type="date"/>

          <input type="submit" value=""/>
        </form>
      </div>
  )
}

export default FillUserData;
